import React, {FunctionComponent} from 'react';
import style from './style.module.scss';
import Button, {ButtonMode, ButtonSize} from "../button";
import { navigate } from "gatsby"

const CallToAction: FunctionComponent<{ title: string, description: string, button: { text: string, link: string } }> = ({button, title, description}) => {
  return (
    <div className={`${style.cta}`}>
      <img src={require('./bg.svg')}/>
      <div className={style.content}>
        <h1>{title}</h1>
        <p>{description}</p>
        <Button buttonMode={ButtonMode.OUTLINE} buttonSize={ButtonSize.BIG} onClick={() => navigate(button.link)}>{button.text}</Button>
      </div>
    </div>
  );
};

export default CallToAction;
