import React, {FunctionComponent, useEffect, useRef} from 'react';
import style from './style.module.scss';
import Button, {ButtonSize} from "../button";

const PLAY_ICON = "data:image/png;base64,";
const PAUSE_ICON = "data:image/png;base64,";

const PlayButton: FunctionComponent<{ mp3: string }> = ({mp3}) => {

  const [, forceUpdate] = React.useState(0);
  const [loading, setLoading] = React.useState(false);

  const voiceRef = useRef<any>();
  var isPaused = voiceRef.current ? voiceRef.current.paused : true;

  useEffect(() => {
    const update = () =>{
      forceUpdate(n => n + 1);
      setTimeout(() => setLoading(false), 500);
    }

    voiceRef.current.addEventListener('pause', update);
    voiceRef.current.addEventListener('play', update);
  })

  return (
    <div className={`${style.playButton} ${loading ? style.loading : ''}`} onClick={() => {
      // @ts-ignore
      [...document.getElementsByTagName("audio")]
        .filter(e => e !== voiceRef.current)
        .forEach(audio => audio.pause());

      if (voiceRef.current.paused) {
        voiceRef.current.play()
      } else {
        voiceRef.current.pause()
      }

      setLoading(true);
    }}>
      <img alt={isPaused ? 'play icon' : 'pause icon'} src={isPaused ? PLAY_ICON : PAUSE_ICON}/>
      <audio controls ref={voiceRef} className={style.mp3}>
        <source src={mp3} type="audio/mpeg"/>
      </audio>
    </div>
  );
};

export default PlayButton;
